<template>
  <v-container fluid>
    <v-layout row wrap>
      <v-flex xs12 sm3>
        <v-parallax
        :src="require('@/assets/event.png')"
        height="600"
        class="hero-img">
          <v-container fill-height>
            <v-layout align-top>
              <v-flex text-xs-center>
                <h1 class="display-1 font-weight-thin">Events</h1>
              </v-flex>
            </v-layout>
          </v-container>
        </v-parallax> 
      </v-flex>
      <v-flex xs12 sm9>
        <v-container fill-height>
          <v-layout>
            <v-flex>
              <v-layout 
              row 
              wrap
              class="event-list">
                <v-flex
                  v-for="(event, index) in events"
                  :key="index"
                  xs12
                >
                  <v-card class="my-2">
                    <v-layout row>
                      <v-flex xs6>
                        <v-card-title primary-title>
                          <div>
                            <div class="headline">{{event.title}}</div>
                            <span class="grey-text">{{event.location}}</span>
                            <div>{{event.date}}</div>
                          </div>
                        </v-card-title>
                      </v-flex>
                      <v-flex xs6>
                        <v-card-media
                          :src="require('@/assets/' + event.img + '.png')"
                          height="150px"
                          contain
                          class="my-2"
                        ></v-card-media>
                      </v-flex>
                    </v-layout>
                    <v-divider light></v-divider>
                    <v-card-actions class="pa-3">
                      <v-btn flat color="success" @click="show = !show">
                        <div>{{ show ? 'Read Less' : 'Read More' }}</div>
                      </v-btn>
                      <v-spacer></v-spacer>
                      <v-btn icon @click="show = !show">
                        <v-icon>{{ show ? 'keyboard_arrow_up' : 'keyboard_arrow_down' }}</v-icon>
                      </v-btn>
                    </v-card-actions>

                    <v-slide-y-transition>
                      <v-card-text v-show="show">
                        {{event.description}}
                      </v-card-text>
                    </v-slide-y-transition>
                  </v-card>
                </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      show: false,
      events: [
        {
          img: 'logo',
          title: 'Founding of CCITICS',
          location: 'CCITICS office',
          description: 'The Chamber of Commerce, Industry, Tourism and Integration Colombia Singapore - CCITICS - was founded on Singapore National Day in Bogotá, Colombia by Mario Jose Lizana-Allende and Cecil Yi Peng Ang. We chose this symbolic day to start fostering a cross-Pacific relationship between two countries with hitherto little trade and diplomatic relations because the birthday of Singapore reminded us that nothing is impossible, even for a small island state with limited resources, if we are willing to work intelligently, compassionately and with fortitude. There are many opportunities to explore between Singapore and Colombia, and we hope that with the support of our members, CCITICS will be able to fulfill its promise as the voice of business between the two countries.',
          date: '09 Aug 2018'
        },
        {
          img: 'enterprisesg',
          title: 'Meeting with Singapore Trade Representative',
          location: 'Crepes and Waffles Parque 93',
          description: 'The founding committee was honored to have an audience with Singapore Government Regional Trade Director Ms. Sufei Tan. Ms. Tan is based in Mexico City and served over 6 years as a representative of Enterprise SG, a Singapore Government agency that promotes trade. We discussed several business ideas and Ms. Tan gracefully offered her help in strengthening ties between Colombia and Singapore. Thank you Sufei for your visit!',
          date: '23 Aug 2018'
        },
        {
          img: 'jorgecardenas',
          title: 'Meeting with Don Jorge',
          location: 'Don Jorge office',
          description: 'The founding committee was honored to have an audience with pre-eminent Colombian statesman and leading businessman Jorge Cardenas. Don Jorge served over 40 years as the Chairman of the Colombian Coffee Growers Federation and is a strong proponent of Colombian businesses. We discussed several business ideas and Don Jorge was kind enough to provide us with the necessary guidance. Thank you Don Jorge for your time!',
          date: '23 Aug 2018'
        }
      ]
    }
  }
}
</script>

<!-- CSS scoped for this page only -->
<style scoped>
  .event-list {
    position: absolute;
    top: 4%;
    bottom: 4%;
    right: 5%;
    left: 28%;
    overflow: auto;
  }

  @media only screen and (max-width: 400px) {
    .hero-img {
      height: 300px;
    }
    .event-list {
      position: relative;
      top: 0%;
      bottom: 0%;
      right: 0%;
      left: 0%;
      overflow: auto;
    }
  }
</style>



