<template>
  <v-container fluid>
    <v-layout row wrap>
      <v-flex xs12 sm3>
        <v-parallax
        :src="require('@/assets/event.png')"
        height="600"
        class="hero-img">
          <v-container fill-height>
            <v-layout align-top>
              <v-flex text-xs-center>
                <h1 class="display-1 font-weight-thin">Eventos</h1>
              </v-flex>
            </v-layout>
          </v-container>
        </v-parallax> 
      </v-flex>
      <v-flex xs12 sm9>
        <v-container fill-height>
          <v-layout>
            <v-flex>
              <v-layout 
              row 
              wrap
              class="event-list">
                <v-flex
                  v-for="(event, index) in events"
                  :key="index"
                  xs12
                >
                  <v-card class="my-2">
                    <v-layout row>
                      <v-flex xs6>
                        <v-card-title primary-title>
                          <div>
                            <div class="headline">{{event.title}}</div>
                            <span class="grey-text">{{event.location}}</span>
                            <div>{{event.date}}</div>
                          </div>
                        </v-card-title>
                      </v-flex>
                      <v-flex xs6>
                        <v-card-media
                          :src="require('@/assets/' + event.img + '.png')"
                          height="150px"
                          contain
                          class="my-2"
                        ></v-card-media>
                      </v-flex>
                    </v-layout>
                    <v-divider light></v-divider>
                    <v-card-actions class="pa-3">
                      <v-btn flat color="success" @click="show = !show">
                        <div>{{ show ? 'Leer Menos' : 'Leer Mas' }}</div>
                      </v-btn>
                      <v-spacer></v-spacer>
                      <v-btn icon @click="show = !show">
                        <v-icon>{{ show ? 'keyboard_arrow_up' : 'keyboard_arrow_down' }}</v-icon>
                      </v-btn>
                    </v-card-actions>

                    <v-slide-y-transition>
                      <v-card-text v-show="show">
                        {{event.description}}
                      </v-card-text>
                    </v-slide-y-transition>
                  </v-card>
                </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      show: false,
      events: [
        {
          img: 'logo',
          title: 'Fundación de CCITICS',
          location: 'Oficina de CCITICS',
          description: 'La Cámara de Comercio, Industria, Turismo e Integración Colombia Singapur - CCITICS - fundada el Día Nacional de Singapur en Bogotá, Colombia por Mario José Lizana-Allende y Cecil Yi Peng Ang. Elegimos este día, simbólicamente, para comenzar a fomentar la relación comercial y diplomática, hasta ahora pequeña, entre dos países, porque el cumpleaños de Singapur nos recordó que nada es imposible, incluso para un pais pequeño e insular con recursos limitados, si estamos dispuestos a trabajar de manera inteligente, compasiva y con fortaleza. Hay muchas oportunidades para explorar entre Singapur y Colombia, y esperamos que con el apoyo de nuestros miembros, CCITICS pueda cumplir su promesa como la voz de negocios entre los dos paises.',
          date: '09 Aug 2018'
        },
        {
          img: 'enterprisesg',
          title: 'Reunión con el representante comercial de Singapur',
          location: 'Crepes & Waffles Parque 93',
          description: 'El comité fundador tuvo el honor de tener una audiencia con la Directora de Comercio Regional del Gobierno de Singapur, Sra. Sufei Tan. La Sra. Tan tiene su sede en la Ciudad de México y se desempeñó durante más de 6 años como representante de Enterprise SG, una agencia del gobierno de Singapur que promueve el comercio. Discutimos varias ideas de negocios y la Sra. Tan ofreció su ayuda para fortalecer los lazos entre Colombia y Singapur. ¡Gracias Sufei por tu visita!',
          date: '23 Aug 2018'
        },
        {
          img: 'jorgecardenas',
          title: 'Reunión on Don Jorge',
          location: 'Oficina de Don Jorge',
          description: 'El comité fundador tuvo el honor de tener una audiencia con el prominente estadista colombiano y empresario líder, Jorge Cárdenas. Don Jorge se desempeñó durante más de 40 años como Presidente de la Federación Nacional de Cafeteros de Colombia y es un firme defensor de las empresas colombianas. Discutimos varias ideas de negocios y Don Jorge tuvo la amabilidad de proporcionarnos la orientación necesaria. ¡Gracias Don Jorge por tu tiempo!',
          date: '23 Aug 2018'
        }
      ]
    }
  }
}
</script>

<!-- CSS scoped for this page only -->
<style scoped>
  .event-list {
    position: absolute;
    top: 4%;
    bottom: 4%;
    right: 5%;
    left: 28%;
    overflow: auto;
  }

  @media only screen and (max-width: 400px) {
    .hero-img {
      height: 300px;
    }
    .event-list {
      position: relative;
      top: 0%;
      bottom: 0%;
      right: 0%;
      left: 0%;
      overflow: auto;
    }
  }
</style>



